.menu_wrapper {
  border-radius: 50%;
  margin: 10px 20px 10px 0px;
  display: inline-block;
  cursor: pointer;
  }

  .menu_wrapper:hover {
   border-radius: 50%;
  }
.drawer_off{
  background-color: gray;
  transition: 500ms;
  border: 2px solid gray;
  position: absolute;
  right: 100%;
  width: 75%;
  height: 84.5vh;
  color: aqua;
}
.drawer_on{
  background-color: gray;
  transition: 500ms;
  border: 2px solid gray;
  position: absolute;
  right: 25%;
  width: 75%;
  height: 84.5vh;
  color: aqua;
}
  
.bar1, .bar2, .bar3 {
   width: 35px;
   height: 5px;
   background-color: white;
   margin: 6px 0;
   transition: 0.4s;
  }
  
  .change .bar1 {
   -webkit-transform: rotate(-45deg) translate(-9px, 6px);
   transform: rotate(-45deg) translate(-9px, 6px);
  }
  
  .change .bar2 {opacity: 0;}
  
  .change .bar3 {
   -webkit-transform: rotate(45deg) translate(-8px, -8px);
   transform: rotate(45deg) translate(-8px, -8px);
  }